.select2-container--bootstrap-5 {
  .select2-selection--multiple {
    // List items
    .select2-selection__rendered {
      display: flex;
      flex-direction: row;
      flex-wrap: wrap;
      padding-left: 0;
      margin: 0;
      list-style: none;
      padding-top: 2px;

      // Items
      .select2-selection__choice {
        display: flex;
        flex-direction: row;
        align-items: center;
        padding: $s2bs5-multi-item-padding-y $s2bs5-multi-item-padding-x;
        margin-right: 0.375rem;
        margin-top: 0.375rem;
        margin-bottom: 0.375rem;
        @include font-size($s2bs5-font-size-sm);
        color: $s2bs5-color;
        cursor: auto;
        border: $s2bs5-border-width solid $s2bs5-border-color;
        @include border-radius($s2bs5-border-radius);

        // Removal button
        .select2-selection__choice__remove {
          width: $s2bs5-clear-width;
          height: $s2bs5-clear-height;
          padding: $s2bs5-clear-padding-y $s2bs5-clear-padding-x;
          margin-right: 0.25rem;
          overflow: hidden;
          text-indent: 100%;
          white-space: nowrap;
          background: $s2bs5-clear-bg;
          border: 0;

          &:hover {
            background: $s2bs5-clear-hover-bg;
          }

          // Remove children
          > span {
            display: none;
          }
        }
      }
    }

    // Input area
    .select2-search {
      display: block;
      width: 100%;
      height: $s2bs5-height-inner;

      // Input field
      .select2-search__field {
        width: 100%;
        height: $s2bs5-height-inner;
        margin-top: 0;
        margin-left: 0;
        font-family: $s2bs5-font-family;
        line-height: $s2bs5-line-height;
        background-color: transparent;
        resize: none;
      }
    }

    // Clear button override
    .select2-selection__clear {
      right: $s2bs5-padding-x;
    }
  }
}
